import React, { useEffect, useRef, useState } from 'react'

import Box from './Box'

const LazyLoad = ({ ratio, children }) => {
  const [visible, setVisible] = useState()
  const ref = useRef()
  useEffect(() => {
    const options = {
      root: null,
      rootMargin: '0px',
      threshold: 0.1
    }

    const observer = new IntersectionObserver(e => {
      if (e && e[0]) {
        setVisible(v => v || e[0].isIntersecting)
      }
    }, options);

    observer.observe(ref.current)
  }, [])
  return (
    <div ref={ref}>
      {visible ? children : <Box pt={`${100 / ratio}%`} />}
    </div>
  )
}

export default LazyLoad
