import React from 'react'
import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import Button from '../../../components/Button'
import Source from '../../../components/Source'
import Container from '../../../components/Container'
import theme, { responsive } from '../../../components/ThemeProvider/theme'

import Table from './Table'
import { trackClick } from '../../../utils/trackEvent'


const TypeText = ({ type, currency }) => <Text><Text.Inline fontWeight="bold" color="custom.textBlue">{type}</Text.Inline>{currency}</Text>

const fundData = [
  {
    th: '',
    td: ''
  },
  {
    th: '/',
    td: ' ESG'
  },
  {
    th: '',
    td: 'RR3'
  },
  {
    th: '*',
    td: (
      <Box>
        <TypeText type="A" currency="" />
        <TypeText type="B" currency="" />
        <TypeText type="NA()" currency="" />
        <TypeText type="NB()" currency="" />
      </Box>
    )
  },
  {
    th: '*',
    td: '1.70%'
  },
  {
    th: '',
    td: '0.26%'
  },
  {
    th: '',
    td: '90'
  },
  {
    th: '',
    td: '7'
  },
]

const links = [
  {
    text: 'D M',
    href: ''
  },
  {
    text: '',
    href: ''
  },
  {
    text: '',
    href: ''
  },
]

const Fund = () => {
  return (
    <Box bg="rgba(240,244,244,0.71)" pt={theme.headerHeight}>
      <Container>
        <Text.Bold textAlign="center" color="custom.blue" fontSize={responsive('1.5em', '2.5em')}></Text.Bold>
        <Table data={fundData} mt="1.75em" mx="auto" width={responsive('auto', '80%')} />
        <Source borderColor="custom.borderBlue">
          2021/6/30<Text.Inline fontWeight="bold"> RR1-RR5 </Text.Inline> *
        </Source>
        <Flex alignItems="center" flexDirection={responsive('column', 'row')}>
          {links.map(({ text, href }, k) => (
            <Box width={responsive('100%', 1 / 3)} mt="1em" mx={responsive(0, '0.5em')} key={k}>
              <Button onClick={() => trackClick(text)} borderRadius="1.5em" width="100%" py="1rem" href={href} fontSize={responsive('1em', '1.25em')}>{text}</Button>
            </Box>
          ))}
        </Flex>
      </Container>
    </Box>
  )
}

export default Fund
